import React, { useEffect, useState } from "react"
import { TEST_VERSION } from "../plugins/version";

export default ({ }:any) => {

    const [messages, setMessages ] = useState<any>([])
    useEffect(()=>{

        const eventSource = new EventSource("https://dialer-redis-streams-worker.makethemcall.workers.dev/events");

        eventSource.onmessage = (event) => {
            console.log("New update:", event.data);
            setMessages((prevMessages: any) => [...prevMessages, event.data]);
        };

        return () => eventSource.close(); // Clean up when component unmounts
    },[])

    return (<>
        VERSION: {TEST_VERSION}<br/>
        {messages.map(( item : any, index : number )=>{
            return <p>
                {item}
            </p>
        })}
    </>)
}

